<template>
  <li :class="[hasChildren ? 'tree-node': 'tree-leaf', {'opened-node': hasChildren}]">
    <i @click="toggleOpen"></i>

    <span class="node-container" @click="select">
      <company-card v-if="node.logo && node.name" :company="node" size="s"/>
    </span>

    <transition @enter="enter"
                @after-enter="clearHeight"
                @leave="leave"
                @after-leave="clearHeight"
                name="tree-slide">
      <TreeNode v-show="isOpen" :nodes="node[state.childrenProp]" :state="state"></TreeNode>
    </transition>
  </li>
</template>


<script>
  import CompanyCard from '@/components-app/CompanyCard'

  const openedNodeClass = 'opened-node'
  const selectedNodeClass = 'selected-node'
  // const closedNodeClass = 'closed-node'

  export default {
    name: 'TreeLeaf',
    components: {
      TreeNode: () => import('./TreeNode'),
      CompanyCard,
    },
    created() {
      var self = this
      // Listen for comands from top Tree component
      self.state.$on('expandAll', function() {
        self.isOpen = true
      })
      self.state.$on('collapseAll', function() {
        self.isOpen = false
      })
    },
    mounted() {
      if (this.preselectedCompany && this.preselectedCompany === this.node.id) {
        this.select()
      }
    },
    props: ['node', 'state', 'preselectedCompany'],
    data: function() {
      return {
        hasChildren: this.node[this.state.childrenProp] && this.node[this.state.childrenProp].length > 0,
        isOpen: true,
      }
    },
    methods: {
      toggleOpen(event) {
        var nodeEl = event.target.parentNode
        nodeEl.classList.toggle(openedNodeClass)
        this.isOpen = !this.isOpen
      },
      select(event) {
        this.$el.classList.add(selectedNodeClass)
        this.state.selectNode(this.node, this.deselect)
      },
      deselect() {
        this.$el.classList.remove(selectedNodeClass)
      },
      enter(el) {
        el.style.height = 'auto'
        const realHeight = getComputedStyle(el).height
        el.style.height = '0px'

        /* eslint-disable no-unused-expressions */
        el.offsetHeight // Force repaint

        el.style.height = realHeight
      },
      leave(el) {
        el.style.height = 'auto'
        const realHeight = getComputedStyle(el).height
        el.style.height = realHeight

        /* eslint-disable no-unused-expressions */
        el.offsetHeight // Force repaint

        el.style.height = '0px'
      },
      clearHeight(el) {
        el.style.height = null
      },
    },
  }
</script>



